import React, { useState } from 'react';
import {useSignMessage} from "wagmi";
import { useAccount } from "wagmi";

interface Props {}

const MintButton: React.FC<Props> = () => {
    const [isMinting, setIsMinting] = useState(false);
    const { address} = useAccount();
    let sf = (window as any).Shopify
    if(sf == undefined){
        sf  = {
            checkout: {
                email: "dummy@example.com",
                orderId: "738289383773"
            }
        }
    }

    const { data:signature, isError:isSigningFailed, isLoading:isSigning, isSuccess:isSignedSuccessfully, signMessageAsync } = useSignMessage({
        message: sf.checkout.email,
    })

    const handleClick = async () => {
        setIsMinting(true);
        await signMessageAsync()

        if(isSignedSuccessfully){
            try {
                const response = await fetch('/apps/hstokengate/public/mint',
                    {
                        "method": "POST",
                        headers: {"Content-Type": "application/json"},
                        body: JSON.stringify({
                            "signedMessage": signature,
                            "walletAddress": address,
                            "orderId": sf.checkout.orderId
                        })});

                if (response.ok) {
                    console.log(response.json());
                } else if(response.status == 204){
                    //already minted
                    console.log(response.statusText);
                } else if(response.status == 401){
                    //invalid signature
                    console.error(response.statusText);
                } else if(response.status == 402){
                    //please comlpete payment first and come back again
                    console.error(response.statusText);
                } else if(response.status == 409){
                    //Sth went wrong, contact us
                    console.error(response.statusText);
                } else{
                    console.error("unknown error please contact service")
                }
            } catch (error) {
                console.error('Error sending POST request:', error);
            }
        }


        setIsMinting(false);
    };

    return (
        <div
            style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
            }}>
            <button onClick={handleClick} disabled={isMinting || isSigning}>
                {isMinting || isSigning ? 'Your NFT is minting...' : 'Mint'}
            </button>
        </div>
    );
};

export default MintButton;
